}

.singleProductContainer .viewLess {
    color: red !important;
    font-weight: normal !important;
    font-size: 12px;
    position: absolute;
    cursor:pointer;
    left: 5%;
    bottom:3%;
}

.singleProductContainer .availOffers{
    padding:10px;
    background:#fff;
    border-radius:10px;
    margin:10px 0px;
    position:relative;
    height:105px;
    overflow:hidden;
    padding-bottom:25px;
}

.singleProductContainer .availOffers>div{
    height:65px;
    overflow: hidden;
}


