.productShare{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.menu {
    z-index: 999;
    position: relative;
    display: inline-block;
    padding: 0;
    margin: 0;
    list-style-type: none;
  }
  .menu .share i.fa {
    height: 40px;
    width: 40px;
    text-align: center;
    line-height: 40px;
    background-color: #e0e0e0;
    border-radius: 50%;
  }
  .menu.active .share.bottom .submenu li:nth-child(1) {
    opacity: 1;
    top: 50px;
    transform: rotate(0deg);
    transition-delay: 0.08s;
  }
  .menu.active .share.bottom .submenu li:nth-child(2) {
    opacity: 1;
    top: 100px;
    transform: rotate(0deg);
    transition-delay: 0.16s;
  }
  .menu.active .share.bottom .submenu li:nth-child(3) {
    opacity: 1;
    top: 150px;
    transform: rotate(0deg);
    transition-delay: 0.24s;
  }
  .menu.active .share.bottom .submenu li:nth-child(4) {
    opacity: 1;
    top: 200px;
    transform: rotate(0deg);
    transition-delay: 0.32s;
  }
  .menu.active .share.bottom .submenu li:nth-child(5) {
    opacity: 1;
    top: 250px;
    transform: rotate(0deg);
    transition-delay: 0.4s;
  }
  .menu .submenu {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  .menu .submenu li {
    transition: all ease-in-out 0.5s;
    position: absolute;
    top: 0;
    left: 5px;
    z-index: -1;
    opacity: 0;
  }
  .menu .submenu li a {
    color: #212121;
  }
  .menu .submenu li a:hover i.fa {
    color: #fff;
  }
  .menu .submenu li a:hover.facebook i.fa {
    background-color: #3b5999;
  }
  .menu .submenu li a:hover.twitter i.fa {
    background-color: #55acee;
  }
  .menu .submenu li a:hover.googlePlus i.fa {
    background-color: #dd4b39;
  }
  .menu .submenu li a:hover.instagram i.fa {
    background-color: #e4405f;
  }
  .menu .submenu li:nth-child(1) {
    transform: rotateX(45deg);
  }
  .menu .submenu li:nth-child(2) {
    transform: rotateX(90deg);
  }
  .menu .submenu li:nth-child(3) {
    transform: rotateX(135deg);
  }
  .menu .submenu li:nth-child(4) {
    transform: rotateX(180deg);
  }

  .productImageSlider .slick-next{
    right:28px;
    filter: grayscale(1) invert(1);
  }
  .productImageSlider .slick-prev{
    z-index:100;
    left:16px;
    filter: grayscale(1) invert(1);
  }

  .productImageSlider .card{
    border:none;
  }

  .mobileSingleProduct .cardTitle{
    font-weight: 500 !important;
    color:#000;
    font-size:18px;
  }

  .descChild img{
    width:100%;
    height:100%;
  }

  .offers .availOffers li:nth-child(n+3){
    display: none;
  }

  .offers .availOffers.expandOffersCard li:nth-child(n+3){
    display: list-item;
  }

